body {
  font-family: 'Montserrat', Arial, sans-serif;
  line-height: 1.6;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
}

.black-nav {
  background: gray;
  align-items: center;
  width: 50%;
  display: flex;
  color: white;
  padding: 20px;
  margin: auto;
  border-radius: 10px;
  justify-content: center;
}

.list {
  align-items: center;
  background-color: rgb(185, 185, 185);
  border-radius: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 10px auto;
  max-width: 950px;
  padding: 10px;
  width: 100%;
}


.modal {
  justify-content: space-between;
  padding: 20px;
  background: #eee;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
  margin: auto; 
}

.publish {
  margin: 10px auto;
  display: flex; 
  justify-content: center;
}

input {
  width: 30%;
  display: flex; 
  margin: 0 auto;
}

input[type="text" i] {
  padding: 1px 2px;
}